    comicListID(state) {
      // 
      // for(var as=0;as <state.comicList.length;as++){
      //   state.comicList.info[as] = state.comicList.info[as].slice(0, 6);
      // }
      // ID
      for (var i = 0; i < state.comicList.length; i++) {
        for (var j = 0; j < state.comicList[i].info.length; j++) {
          state.comicList[i].info[j].comic_idOld = state.comicList[i].info[j].comic_id;
          var id = String(state.comicList[i].info[j].comic_id);
          if (id.length == 1) {
            id = '000/000/00' + id;
            state.comicList[i].info[j].comic_id = id;
          } else if (id.length == 2) {
            id = '000/000/0' + id;
            state.comicList[i].info[j].comic_id = id;
          } else if (id.length == 3) {
            id = '000/000/' + id;
            state.comicList[i].info[j].comic_id = id;
          } else if (id.length == 4) {
            id = '000/00' + id.slice(0, 1) + '/' + id.slice(1);
            state.comicList[i].info[j].comic_id = id;
          }
        }
      }
      // 
      for (var n = 0; n < state.comicList.length; n++) {
        for (var m = 0; m < state.comicList[n].info.length; m++) {
          var hot = String(state.comicList[n].info[m].hot);
          // 
          if (hot.length > 8) {
            if ((hot / 100000000).toFixed(1).slice(-1) != '0') {
              state.comicList[n].info[m].hot = (hot / 100000000).toFixed(1) + '';
            } else {
              state.comicList[n].info[m].hot = Math.floor(hot / 100000000) + '';
            }
            // 
          } else {
            if ((hot / 10000).toFixed(1).slice(-1) != '0') {
              state.comicList[n].info[m].hot = (hot / 10000).toFixed(1) + '';
            } else {
              state.comicList[n].info[m].hot = Math.floor(hot / 10000) + '';
            }
          }
        }
      }
    },
